import React from 'react';
import { Card, Button, message, Popconfirm } from 'antd';
import { LinkOutlined, DeleteOutlined } from '@ant-design/icons';
import { GitHubService } from '../../services/github';
import { AuthService } from '../../services/auth';

const WebsiteCard = ({ website, onRefresh }) => {
  const { title, url, description, id } = website;
  const isLoggedIn = AuthService.isAuthenticated();

  // 
  const getFaviconUrl = (websiteUrl) => {
    try {
      const urlObj = new URL(websiteUrl);
      //  favicon.ico 
      return `${urlObj.origin}/favicon.ico`;
    } catch (error) {
      //  Google 
      return `https://www.google.com/s2/favicons?domain=${encodeURIComponent(websiteUrl)}&sz=64`;
    }
  };

  const handleDelete = async () => {
    try {
      const success = await GitHubService.deleteWebsite(id);
      if (success) {
        message.success('');
        window.location.reload(); // 
      }
    } catch (error) {
      message.error(error.message);
    }
  };

  return (
    <Card
      hoverable
      style={{ 
        boxShadow: '0 1px 2px rgba(0,0,0,0.1)',
        height: '100%'  // 
      }}
      cover={
        <div style={{ 
          padding: '16px', 
          display: 'flex', 
          justifyContent: 'center', 
          alignItems: 'center',
          background: '#f5f5f5',
          height: '80px' // 
        }}>
          <img 
            alt={title} 
            src={getFaviconUrl(url)}
            style={{ 
              width: '32px', 
              height: '32px',
              objectFit: 'contain'
            }}
            onError={(e) => {
              //  Google 
              if (!e.target.dataset.tried) {
                e.target.dataset.tried = 'true';
                e.target.src = `https://www.google.com/s2/favicons?domain=${encodeURIComponent(url)}&sz=64`;
              } else {
                e.target.style.display = 'none';
              }
            }}
          />
        </div>
      }
      actions={[
        <Button 
          key="visit" 
          type="link" 
          icon={<LinkOutlined />}
          onClick={() => window.open(url, '_blank')}
        >
          
        </Button>,
        isLoggedIn && (
          <Popconfirm
            key="delete"
            title=""
            onConfirm={handleDelete}
            okText=""
            cancelText=""
          >
            <Button 
              type="link" 
              danger
              icon={<DeleteOutlined />}
            >
              
            </Button>
          </Popconfirm>
        )
      ].filter(Boolean)}
    >
      <Card.Meta
        title={title}
        description={description}
      />
    </Card>
  );
};

export default WebsiteCard; 