import React, { useState } from 'react';
import { Layout, Button, Modal, message } from 'antd';
import { GithubOutlined, PlusOutlined, LogoutOutlined } from '@ant-design/icons';
import TokenInput from '../TokenInput';
import { AuthService } from '../../services/auth';
import AddWebsiteForm from '../AddWebsiteForm';
import { GitHubService } from '../../services/github';

const { Header, Content, Footer } = Layout;

// 
const styles = {
  header: {
    backgroundColor: 'white',
    boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
    padding: '0 20px'
  },
  headerContent: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    height: '100%'
  },
  title: {
    fontSize: '20px',
    fontWeight: 'bold'
  },
  buttonGroup: {
    display: 'flex',
    gap: '8px'
  },
  content: {
    padding: '24px',
    minHeight: '100vh'
  },
  footer: {
    textAlign: 'center',
    color: '#666'
  }
};

const AppLayout = ({ children }) => {
  const [isTokenModalOpen, setIsTokenModalOpen] = useState(false);
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const isLoggedIn = AuthService.isAuthenticated();

  // 
  const handleAddClick = () => {
    if (!isLoggedIn) {
      //  token token 
      setIsTokenModalOpen(true);
    } else {
      //  token 
      setIsAddModalOpen(true);
    }
  };

  // 
  const handleLogin = () => {
    setIsTokenModalOpen(true);
  };

  // 
  const handleLoginSuccess = () => {
    setIsTokenModalOpen(false);
    window.location.reload(); // 
  };

  const handleSubmit = async (values) => {
    try {
      await GitHubService.createWebsite(values);
      message.success('');
      setIsAddModalOpen(false);
      window.location.reload(); // 
    } catch (error) {
      message.error(error.message);
    }
  };

  return (
    <Layout>
      {/*  Header */}
      <Header style={styles.header}>
        <div style={styles.headerContent}>
          {/*  */}
          <h1 style={styles.title}></h1>
          {/*  */}
          <div style={styles.buttonGroup}>
            {/*  */}
            <Button 
              type="primary"
              icon={<PlusOutlined />}
              onClick={handleAddClick}
            >
              
            </Button>
            {isLoggedIn ? (
              <Button 
                icon={<LogoutOutlined />}
                onClick={() => AuthService.logout()}
              >
                
              </Button>
            ) : (
              <Button 
                type="primary"
                icon={<GithubOutlined />}
                onClick={handleLogin}
              >
                
              </Button>
            )}
          </div>
        </div>
      </Header>

      {/*  Content */}
      <Content style={styles.content}>
        {children}
      </Content>

      {/*  Footer */}
      <Footer style={styles.footer}>
         GitHub Issues 
      </Footer>

      {/* Token  */}
      <Modal
        title=" GitHub Token"
        open={isTokenModalOpen}
        onCancel={() => setIsTokenModalOpen(false)}
        footer={null}
      >
        <TokenInput onLogin={handleLoginSuccess} />
        <div style={{ marginTop: '16px', color: '#666', fontSize: '14px' }}>
          <p> Token</p>
          <ol style={{ paddingLeft: '20px' }}>
            <li> GitHub Settings {'->'} Developer settings</li>
            <li> Personal access tokens {'->'} Tokens (classic)</li>
            <li> token repo </li>
          </ol>
        </div>
      </Modal>

      {/*  */}
      <AddWebsiteForm
        open={isAddModalOpen}
        onCancel={() => setIsAddModalOpen(false)}
        onSubmit={handleSubmit}
      />
    </Layout>
  );
};

export default AppLayout; 