import React, { useState } from 'react';
import { Form, Input, Modal, message } from 'antd';

const AddWebsiteForm = ({ open, onCancel, onSubmit }) => {
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);

  const handleOk = async () => {
    try {
      setLoading(true);
      const values = await form.validateFields();
      await onSubmit(values);
      form.resetFields();
    } catch (error) {
      if (error.errorFields) {
        // 
        return;
      }
      message.error(': ' + error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Modal
      title=""
      open={open}
      onCancel={() => {
        form.resetFields();
        onCancel();
      }}
      onOk={handleOk}
      confirmLoading={loading}
      destroyOnClose
    >
      <Form 
        form={form} 
        layout="vertical"
      >
        <Form.Item
          name="title"
          label=""
          rules={[{ required: true, message: '' }]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          name="url"
          label=""
          rules={[
            { required: true, message: '' },
            { type: 'url', message: 'URL' }
          ]}
          extra="https://github.com"
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          name="description"
          label=""
        >
          <Input.TextArea 
            placeholder="" 
            rows={3}
            showCount
            maxLength={100}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default AddWebsiteForm; 