import { AuthService } from './auth';

const GITHUB_API = 'https://api.github.com';
const REPO_OWNER = 'nbt0';  // 
const REPO_NAME = 'personal-navigation';

export const GitHubService = {
  // 
  async getWebsites() {
    const token = AuthService.getToken();
    try {
      const response = await fetch(
        `${GITHUB_API}/repos/${REPO_OWNER}/${REPO_NAME}/issues?state=open&labels=website`,
        {
          headers: token ? {
            'Authorization': `token ${token}`
          } : {}
        }
      );

      if (!response.ok) {
        throw new Error('');
      }

      const issues = await response.json();
      return issues.map(issue => {
        try {
          const data = JSON.parse(issue.body);
          return {
            id: issue.number,
            title: issue.title,
            ...data
          };
        } catch (error) {
          console.error(':', error);
          return null;
        }
      }).filter(Boolean); // 
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },

  // 
  async createWebsite(website) {
    const token = AuthService.getToken();
    if (!token) {
      throw new Error(' GitHub Token');
    }

    try {
      const response = await fetch(
        `${GITHUB_API}/repos/${REPO_OWNER}/${REPO_NAME}/issues`,
        {
          method: 'POST',
          headers: {
            'Authorization': `token ${token}`,
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            title: website.title,
            body: JSON.stringify({
              url: website.url,
              description: website.description,
              icon: website.icon || 'favicon.ico'
            }),
            labels: ['website']
          })
        }
      );

      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.message || '');
      }

      return response.json();
    } catch (error) {
      console.error(':', error);
      throw new Error(' Token  repo ');
    }
  },

  //  Issue
  async deleteWebsite(id) {
    const token = AuthService.getToken();
    if (!token) {
      throw new Error(' GitHub Token');
    }

    try {
      const response = await fetch(
        `${GITHUB_API}/repos/${REPO_OWNER}/${REPO_NAME}/issues/${id}`,
        {
          method: 'PATCH',
          headers: {
            'Authorization': `token ${token}`,
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            state: 'closed'
          })
        }
      );

      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.message || '');
      }

      return true; // 
    } catch (error) {
      console.error(':', error);
      throw new Error('');
    }
  }
};
